@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,100;1,300&display=swap" rel="stylesheet);

body {
 background-color: #22254b;
 color:white;
 font-family: 'Poppins' ,sans-serif;
 margin: 0;
}


.root {
  display: flex;
  flex-direction: column;

}

*{
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.movie-container{
  display: flex;
  flex-wrap: wrap;
}

.movie{
  width: 300px;
  background-color: #373b69;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0,0,0.1);
  margin: 1rem;
  overflow: hidden;
  position: relative;
}

.movie:hover .info {
  display: block;
  transform: translateY(0); 
  overflow: auto;
}


.movie:hover .info {
  display: block;
  transform: translateY(0); 
  overflow: auto;
}

.info {
  position: absolute;
  display: none;
  bottom: 0;
  left:0;
  right:0;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  max-height: 100;
  overflow: auto;
  background-color: white;
   color: #373b69;
  padding: 1rem;
  transform: translateY(100%);
  transition: all 0.2s ease-in-out;
  
  
}

.movie-info{
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
}



.movie-info  h3{
  margin:0;
}


.movie img {
  max-width: 100%;
}


header {
  padding: 1rem;
  background-color:#373b69 ;
  display: flex;
  justify-content: flex-end;
}


.header form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
}

.search {
 font-family: inherit;
 font-size: 1.2rem;
 border: 2px solid #22254b;
 border-radius: 50px;
 background-color: transparent;
 color: #22254b;
 padding: 0.5rem 1.5rem; 
}

.search:focus{
  outline: none
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

